
body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: #0d1117;
  color: #fff;
}
.container {
  padding: 20px;
  max-width: 960px;
  margin: auto;
}
header {
  text-align: center;
  margin-bottom: 40px;
}
header h1 {
  font-size: 2.5rem;
  color: #ff7b00;
}
.btn {
  background: #1f87ff;
  padding: 10px 20px;
  border-radius: 8px;
  color: white;
  text-decoration: none;
}
section {
  margin-bottom: 40px;
}
.skills {
  list-style: none;
  padding: 0;
}
.skills li {
  background: #1f232c;
  margin: 8px 0;
  padding: 10px;
  border-radius: 6px;
}
footer {
  text-align: center;
  padding: 20px;
  border-top: 1px solid #333;
}
